module.exports = [{"name":"EyeIcon","description":"SVG Eye Icon","code":"import React from \"react\";\n\n/** SVG Eye Icon */\nexport default function EyeIcon() {\n  // Attribution: Fabián Alexis at https://commons.wikimedia.org/wiki/File:Antu_view-preview.svg\n  return (\n    <svg\n      width=\"16\"\n      height=\"16\"\n      xmlns=\"http://www.w3.org/2000/svg\"\n      viewBox=\"0 0 22 22\"\n    >\n      <g transform=\"matrix(.02146 0 0 .02146 1 1)\" fill=\"#4d4d4d\">\n        <path d=\"m466.07 161.53c-205.6 0-382.8 121.2-464.2 296.1-2.5 5.3-2.5 11.5 0 16.9 81.4 174.9 258.6 296.1 464.2 296.1 205.6 0 382.8-121.2 464.2-296.1 2.5-5.3 2.5-11.5 0-16.9-81.4-174.9-258.6-296.1-464.2-296.1m0 514.7c-116.1 0-210.1-94.1-210.1-210.1 0-116.1 94.1-210.1 210.1-210.1 116.1 0 210.1 94.1 210.1 210.1 0 116-94.1 210.1-210.1 210.1\" />\n        <circle cx=\"466.08\" cy=\"466.02\" r=\"134.5\" />\n      </g>\n    </svg>\n  );\n}\n","examples":[{"name":"Example","description":"","code":"import React from 'react';\nimport EyeIcon from 'ps-react/EyeIcon';\n\nexport default function EyeIconExample() {\n  return <EyeIcon />;\n}\n"}]},{"name":"HelloWorld","description":"A super lame good component just say Hello World","props":{"message":{"type":{"name":"string"},"required":false,"description":"Message to display","defaultValue":{"value":"'World'","computed":false}}},"code":"import React from 'react'\r\nimport PropTypes from 'prop-types'\r\n/** A super lame good component just say Hello World */\r\nexport default function HelloWorld ({ message }) {\r\n  return <div>Hello {message}</div>\r\n}\r\n\r\nHelloWorld.propTypes = {\r\n  /** Message to display */\r\n  message: PropTypes.string\r\n}\r\n\r\nHelloWorld.defaultProps = {\r\n  message: 'World'\r\n}\r\n","examples":[{"name":"ExampleHelloWorld","description":"Custom Message","code":"import React from \"react\";\r\nimport HelloWorld from \"ps-react/HelloWorld\";\r\n\r\n/** Custom Message */\r\nexport default function exampleHelloWorld() {\r\n  return <HelloWorld message=\"Segredo Dev\" />;\r\n}\r\n"}]},{"name":"Label","description":"Label with required field display, htmlFor and block styling","props":{"htmlFor":{"type":{"name":"string"},"required":true,"description":""},"label":{"type":{"name":"string"},"required":true,"description":"Label text"},"required":{"type":{"name":"bool"},"required":false,"description":"Display asterisk after label if true"}},"code":"import React from \"react\";\nimport PropTypes from \"prop-types\";\n\n/** Label with required field display, htmlFor and block styling */\n\nexport default function Label({ htmlFor, label, required }) {\n  return (\n    <label style={{ display: \"block\" }} htmlFor={htmlFor}>\n      {label} {required && <span style={{ color: \"red\" }}>*</span>}\n    </label>\n  );\n}\n\nLabel.propTypes = {\n  /**HTML ID for associated input */\n  htmlFor: PropTypes.string.isRequired,\n  /** Label text */\n  label: PropTypes.string.isRequired,\n  /** Display asterisk after label if true */\n  required: PropTypes.bool,\n};\n","examples":[{"name":"ExampleOptional","description":"Optional label","code":"import React from \"react\";\nimport Label from \"ps-react/Label\";\n/** Optional label */\nexport default function ExampleOptional() {\n  return <Label htmlFor=\"test\" label=\"test\" />;\n}\n"},{"name":"ExampleRequired","description":"Required label","code":"import React from \"react\";\nimport Label from \"ps-react/Label\";\n/** Required label */\nexport default function ExampleRequired() {\n  return <Label htmlFor=\"test\" label=\"test\" required />;\n}\n"}]},{"name":"PasswordInput","description":"Password input with integrated label, quality tips, and show password toggle.","props":{"htmlId":{"type":{"name":"string"},"required":true,"description":"Unique HTML ID. Used for tying label to HTML input. Handy hook for automated testing."},"name":{"type":{"name":"string"},"required":true,"description":"Input name. Recommend setting this to match object's property so a single change handler can be used by convention."},"value":{"type":{"name":"any"},"required":false,"description":"Password value"},"label":{"type":{"name":"string"},"required":false,"description":"Input label","defaultValue":{"value":"'Password'","computed":false}},"onChange":{"type":{"name":"func"},"required":true,"description":"Function called when password input value changes"},"maxLength":{"type":{"name":"number"},"required":false,"description":"Max password length accepted","defaultValue":{"value":"50","computed":false}},"placeholder":{"type":{"name":"string"},"required":false,"description":"Placeholder displayed when no password is entered"},"showVisibilityToggle":{"type":{"name":"bool"},"required":false,"description":"Set to true to show the toggle for displaying the currently entered password","defaultValue":{"value":"false","computed":false}},"quality":{"type":{"name":"number"},"required":false,"description":"Display password quality visually via ProgressBar, accepts a number between 0 and 100"},"error":{"type":{"name":"string"},"required":false,"description":"Validation error to display"}},"code":"import React, { useState } from 'react'\nimport PropTypes from 'prop-types'\nimport ProgressBar from '../ProgressBar'\nimport EyeIcon from '../EyeIcon'\nimport TextInput from '../TextInput'\n\n/** Password input with integrated label, quality tips, and show password toggle. */\nexport default function PasswordInput ({ htmlId, value, label, error, onChange, placeholder, maxLength, showVisibilityToggle, quality, ...props }) {\n  const [showPassword, setShowPassword] = useState(false)\n\n  function toggleShowPassword (event) {\n    setShowPassword(!showPassword)\n    if (event) event.preventDefault()\n  }\n\n  return (\n    <TextInput\n      htmlId={htmlId}\n      label={label}\n      placeholder={placeholder}\n      type={showPassword ? 'text' : 'password'}\n      onChange={onChange}\n      value={value}\n      maxLength={maxLength}\n      error={error}\n      required\n      {...props}\n    >\n      {\n        showVisibilityToggle &&\n          <a\n            href='#'\n            onClick={toggleShowPassword}\n            style={{ marginLeft: 5 }}\n          >\n            <EyeIcon />\n          </a>\n      }\n      {\n        value.length > 0 && quality && <ProgressBar percent={quality} width={130} />\n      }\n    </TextInput>\n  )\n}\n\nPasswordInput.propTypes = {\n  /** Unique HTML ID. Used for tying label to HTML input. Handy hook for automated testing. */\n  htmlId: PropTypes.string.isRequired,\n\n  /** Input name. Recommend setting this to match object's property so a single change handler can be used by convention. */\n  name: PropTypes.string.isRequired,\n\n  /** Password value */\n  value: PropTypes.any,\n\n  /** Input label */\n  label: PropTypes.string,\n\n  /** Function called when password input value changes */\n  onChange: PropTypes.func.isRequired,\n\n  /** Max password length accepted */\n  maxLength: PropTypes.number,\n\n  /** Placeholder displayed when no password is entered */\n  placeholder: PropTypes.string,\n\n  /** Set to true to show the toggle for displaying the currently entered password */\n  showVisibilityToggle: PropTypes.bool,\n\n  /** Display password quality visually via ProgressBar, accepts a number between 0 and 100 */\n  quality: PropTypes.number,\n\n  /** Validation error to display */\n  error: PropTypes.string\n}\n\nPasswordInput.defaultProps = {\n  maxLength: 50,\n  showVisibilityToggle: false,\n  label: 'Password'\n}\n","examples":[{"name":"ExampleAllFeatures","description":"All features enabled","code":"import React, { useState } from 'react'\nimport PasswordInput from 'ps-react/PasswordInput'\n\n/** All features enabled */\nexport default function ExampleAllFeatures (props) {\n  const [password, setPassword] = useState('')\n\n  function getQuality () {\n    const length = password.length\n    return length > 10 ? 100 : length * 10\n  }\n\n  return (\n    <PasswordInput\n      htmlId='password-input-example-all-features'\n      name='password'\n      onChange={event => setPassword(event.target.value)}\n      value={password}\n      minLength={8}\n      placeholder='Enter password'\n      showVisibilityToggle\n      quality={getQuality()}\n      {...props}\n    />\n  )\n}\n"}]},{"name":"ProgressBar","description":"","props":{"percent":{"type":{"name":"number"},"required":true,"description":"Percent of progress completed"},"height":{"type":{"name":"number"},"required":false,"description":"Bar height"},"width":{"type":{"name":"number"},"required":true,"description":"Bar width"}},"code":"import React, { Component } from 'react'\nimport PropTypes from 'prop-types'\n\nexport default class ProgressBar extends Component {\n  getColor = (percent) => {\n    if (this.props.percent === 100) return 'green'\n    return this.props.percent > 50 ? 'lightgreen' : 'red'\n  }\n\n  getWidthAsPercentOfTotalWidth = () => {\n    const { percent, width } = this.props\n    return parseInt(width * (percent / 100), 10)\n  }\n  \n  render() {\n    const { percent, width, height = 5 } = this.props\n    return (\n      <div style={{ border: '1px solid lightgray', width: width }}>\n      <div\n        style={{\n          height,\n          width:this.getWidthAsPercentOfTotalWidth(),\n          backgroundColor: this.getColor(percent)\n        }}\n      />\n    </div>\n    )\n  }\n}\n\nProgressBar.propTypes = {\n  /** Percent of progress completed */\n  percent: PropTypes.number.isRequired,\n  /** Bar height */\n  height: PropTypes.number,\n  /** Bar width */\n  width: PropTypes.number.isRequired\n}\n","examples":[{"name":"Example100Percent","description":"Example 100% complete Progress Bar and custom height","code":"import React from \"react\";\nimport ProgressBar from \"ps-react/ProgressBar\";\n\n/** Example 100% complete Progress Bar and custom height*/\nexport default function Example100Percent() {\n  return <ProgressBar percent={100} width={150} height={20} />;\n}\n"},{"name":"Example10Percent","description":"Example 10% complete Progress Bar","code":"import React from 'react'\nimport ProgressBar from 'ps-react/ProgressBar'\n/** Example 10% complete Progress Bar */\nexport default function Example10Percent () {\n  return <ProgressBar percent={10} width={150} />\n}\n"},{"name":"Example70Percent","description":"Example 70% complete Progress Bar","code":"import React from \"react\";\nimport ProgressBar from \"ps-react/ProgressBar\";\n\n/** Example 70% complete Progress Bar */\nexport default function Example70Percent() {\n  return <ProgressBar percent={70} width={150} />;\n}\n"}]},{"name":"RegistrationForm","description":"Registration form with built-in validation","props":{"confirmationMessage":{"type":{"name":"string"},"required":false,"description":"Message displayed upon successful submission","defaultValue":{"value":"'Thanks for registering'","computed":false}},"onSubmit":{"type":{"name":"func"},"required":true,"description":"Called when form is submitted"},"minPasswordLength":{"type":{"name":"number"},"required":false,"description":"Minimum password length","defaultValue":{"value":"8","computed":false}}},"code":"import React, { useState } from 'react'\nimport PropTypes from 'prop-types'\nimport TextInput from '../TextInput'\nimport PasswordInput from '../PasswordInput'\n\n/** Registration form with built-in validation */\nexport default function RegisrationForm ({ confirmationMessage = 'Thanks for registering', minPasswordLength = 8, ...props }) {\n  const [submitted, setSubmitted] = useState(false)\n  const [user, setUser] = useState({ email: '', password: '' })\n  const [errors, setErrors] = useState({})\n\n  function onChange (event) {\n    const { name, value } = event.target\n\n    setUser(prevState => ({\n      ...prevState,\n      [name]: value\n    }))\n  }\n  // Returns a number from 0 to 100 that represents password quality.\n  // For simplicity, just returning % of min length entered.\n  // Could enhance with checks for number, special char, unique characters, etc.\n  function passwordQuality (password) {\n    if (!user.password) return null\n    if (user.password.length >= minPasswordLength) return 100\n    const percentOfMinLength = parseInt(user.password.length / minPasswordLength * 100, 10)\n    return percentOfMinLength\n  }\n\n  function validate ({ email, password }) {\n    const errors = {}\n\n    if (!email) errors.email = 'Email required'\n    if (password.length < minPasswordLength) errors.password = `Password be at least ${minPasswordLength}`\n\n    setErrors(errors)\n\n    const formIsValid = Object.getOwnPropertyNames(errors).length === 0\n    return formIsValid\n  }\n\n  function onSubmit () {\n    const formIsValid = validate(user)\n    if (formIsValid) {\n      props.onSubmit(user)\n      setSubmitted(true)\n    }\n  }\n\n  return (\n    submitted\n      ? <h2>{confirmationMessage}</h2>\n      : <div>\n        <TextInput\n          htmlId='registration-form-email'\n          name='email'\n          value={user.email}\n          onChange={onChange}\n          label='email'\n          error={errors.email}\n          required\n        />\n        <PasswordInput\n          htmlId='registration-form-password'\n          name='password'\n          value={user.password}\n          onChange={onChange}\n          quality={passwordQuality(user.password)}\n          showVisibilityToggle\n          maxLength={50}\n          error={errors.password}\n        />\n\n        <input type='submit' value='Register' onClick={onSubmit} />\n      </div>\n  )\n}\n\nRegisrationForm.propTypes = {\n  /** Message displayed upon successful submission */\n  confirmationMessage: PropTypes.string,\n\n  /** Called when form is submitted */\n  onSubmit: PropTypes.func.isRequired,\n\n  /** Minimum password length */\n\n  minPasswordLength: PropTypes.number\n}\n","examples":[{"name":"ExampleRegistrationForm","description":"","code":"import React from 'react'\r\nimport RegistrationForm from 'ps-react/RegistrationForm'\r\n\r\nexport default function ExampleRegistrationForm () {\r\n  function onSubmit (user) {\r\n    console.log(user)\r\n  }\r\n  return (\r\n    <RegistrationForm onSubmit={onSubmit} />\r\n  )\r\n}\r\n"}]},{"name":"TextInput","description":"Text input with integrated label to enforce consistency in layout, error display, label placement, and required field marker.","props":{"htmlId":{"type":{"name":"string"},"required":true,"description":"Unique HTML ID. Used for tying label to HTML input. Handy hook for automated testing."},"name":{"type":{"name":"string"},"required":true,"description":"Input name. Recommend setting this to match object's property so a single change handler can be used."},"label":{"type":{"name":"string"},"required":true,"description":"Input label"},"type":{"type":{"name":"enum","value":[{"value":"'text'","computed":false},{"value":"'number'","computed":false},{"value":"'password'","computed":false}]},"required":false,"description":"Input type","defaultValue":{"value":"'text'","computed":false}},"required":{"type":{"name":"bool"},"required":false,"description":"Mark label with asterisk if set to true","defaultValue":{"value":"false","computed":false}},"onChange":{"type":{"name":"func"},"required":true,"description":"Function to call onChange"},"placeholder":{"type":{"name":"string"},"required":false,"description":"Placeholder to display when empty"},"value":{"type":{"name":"any"},"required":false,"description":"Value"},"error":{"type":{"name":"string"},"required":false,"description":"String to display when error occurs"},"children":{"type":{"name":"node"},"required":false,"description":"Child component to display next to the input"}},"code":"import React from 'react'\nimport PropTypes from 'prop-types'\nimport Label from '../Label'\n\n/** Text input with integrated label to enforce consistency in layout, error display, label placement, and required field marker. */\nexport default function TextInput ({\n  htmlId,\n  name,\n  label,\n  type = 'text',\n  required = false,\n  onChange,\n  placeholder,\n  value,\n  error,\n  children,\n  ...props\n}) {\n  return (\n    <div style={{ marginBottom: 16 }}>\n      <Label htmlFor={htmlId} label={label} required={required} />\n      <input\n        id={htmlId}\n        type={type}\n        name={name}\n        placeholder={placeholder}\n        value={value}\n        onChange={onChange}\n        style={error && { border: 'solid 1px red' }}\n        {...props}\n      />\n      {children}\n      {error && (\n        <div className='error' style={{ color: 'red' }}>\n          {error}\n        </div>\n      )}\n    </div>\n  )\n}\n\nTextInput.propTypes = {\n  /** Unique HTML ID. Used for tying label to HTML input. Handy hook for automated testing. */\n  htmlId: PropTypes.string.isRequired,\n\n  /** Input name. Recommend setting this to match object's property so a single change handler can be used. */\n  name: PropTypes.string.isRequired,\n\n  /** Input label */\n  label: PropTypes.string.isRequired,\n\n  /** Input type */\n  type: PropTypes.oneOf(['text', 'number', 'password']),\n\n  /** Mark label with asterisk if set to true */\n  required: PropTypes.bool,\n\n  /** Function to call onChange */\n  onChange: PropTypes.func.isRequired,\n\n  /** Placeholder to display when empty */\n  placeholder: PropTypes.string,\n\n  /** Value */\n  value: PropTypes.any,\n\n  /** String to display when error occurs */\n  error: PropTypes.string,\n\n  /** Child component to display next to the input */\n  children: PropTypes.node\n}\n","examples":[{"name":"ExampleError","description":"Required TextBox with error","code":"import React from \"react\";\nimport TextInput from \"ps-react/TextInput\";\n\n/** Required TextBox with error */\nexport default function ExampleError() {\n  return (\n    <TextInput\n      htmlId=\"example-optional\"\n      label=\"First Name\"\n      name=\"firstname\"\n      onChange={() => {}}\n      required\n      error=\"First name is required.\"\n    />\n  );\n}\n"},{"name":"ExampleOptional","description":"Optional TextBox","code":"import React from \"react\";\nimport TextInput from \"ps-react/TextInput\";\n\n/** Optional TextBox */\nexport default function ExampleOptional() {\n  return (\n    <TextInput\n      htmlId=\"example-optional\"\n      label=\"First Name\"\n      name=\"firstname\"\n      onChange={() => {}}\n    />\n  );\n}\n"}]},{"name":"TextInputBEM","description":"Text input with integrated label to enforce consistency in layout, error display, label placement, and required field marker.","props":{"htmlId":{"type":{"name":"string"},"required":true,"description":"Unique HTML ID. Used for tying label to HTML input. Handy hook for automated testing."},"name":{"type":{"name":"string"},"required":true,"description":"Input name. Recommend setting this to match object's property so a single change handler can be used."},"label":{"type":{"name":"string"},"required":true,"description":"Input label"},"type":{"type":{"name":"enum","value":[{"value":"'text'","computed":false},{"value":"'number'","computed":false},{"value":"'password'","computed":false}]},"required":false,"description":"Input type","defaultValue":{"value":"'text'","computed":false}},"required":{"type":{"name":"bool"},"required":false,"description":"Mark label with asterisk if set to true","defaultValue":{"value":"false","computed":false}},"onChange":{"type":{"name":"func"},"required":true,"description":"Function to call onChange"},"placeholder":{"type":{"name":"string"},"required":false,"description":"Placeholder to display when empty"},"value":{"type":{"name":"any"},"required":false,"description":"Value"},"error":{"type":{"name":"string"},"required":false,"description":"String to display when error occurs"},"children":{"type":{"name":"node"},"required":false,"description":"Child component to display next to the input"}},"code":"import React from 'react'\r\nimport PropTypes from 'prop-types'\r\nimport Label from '../Label'\r\n\r\n/** Text input with integrated label to enforce consistency in layout, error display, label placement, and required field marker. */\r\nexport default function TextInputBEM ({\r\n  htmlId,\r\n  name,\r\n  label,\r\n  type = 'text',\r\n  required = false,\r\n  onChange,\r\n  placeholder,\r\n  value,\r\n  error,\r\n  children,\r\n  ...props\r\n}) {\r\n  return (\r\n    <div className='textinput'>\r\n      <Label htmlFor={htmlId} label={label} required={required} />\r\n      <input\r\n        id={htmlId}\r\n        type={type}\r\n        name={name}\r\n        placeholder={placeholder}\r\n        value={value}\r\n        onChange={onChange}\r\n        className={error && 'textinput__input--state-error'}\r\n        {...props}\r\n      />\r\n      {children}\r\n      {error && (\r\n        <div className='textinput__error'>\r\n          {error}\r\n        </div>\r\n      )}\r\n    </div>\r\n  )\r\n}\r\n\r\nTextInputBEM.propTypes = {\r\n  /** Unique HTML ID. Used for tying label to HTML input. Handy hook for automated testing. */\r\n  htmlId: PropTypes.string.isRequired,\r\n\r\n  /** Input name. Recommend setting this to match object's property so a single change handler can be used. */\r\n  name: PropTypes.string.isRequired,\r\n\r\n  /** Input label */\r\n  label: PropTypes.string.isRequired,\r\n\r\n  /** Input type */\r\n  type: PropTypes.oneOf(['text', 'number', 'password']),\r\n\r\n  /** Mark label with asterisk if set to true */\r\n  required: PropTypes.bool,\r\n\r\n  /** Function to call onChange */\r\n  onChange: PropTypes.func.isRequired,\r\n\r\n  /** Placeholder to display when empty */\r\n  placeholder: PropTypes.string,\r\n\r\n  /** Value */\r\n  value: PropTypes.any,\r\n\r\n  /** String to display when error occurs */\r\n  error: PropTypes.string,\r\n\r\n  /** Child component to display next to the input */\r\n  children: PropTypes.node\r\n}\r\n","examples":[{"name":"ExampleError","description":"Required TextBox with error","code":"import React from 'react';\nimport TextInputBEM from 'ps-react/TextInputBEM';\n\n/** Required TextBox with error */\nexport default function ExampleError () {\n  return (\n    <TextInputBEM\n      htmlId='example-optional'\n      label='First Name'\n      name='firstname'\n      onChange={() => {}}\n      required\n      error='First name is required.'\n    />\n  )\n}\n"}]}]